<template>
  <el-dialog
    v-model="initData.visible"
    class="custom-dialog"
    width="560px"
    :close-on-click-modal="false"
    @closed="handleClose"
  >
    <template #header>
      <div style="width: 100%; text-align: center">
        <h3>合规业务知晓书</h3>
      </div>
    </template>
    <div style="font-size: 14px">
      <p class="content" style="text-indent: 2em">
        鉴于我司此次业务违规给贵方带来不良影响，我司承诺做出如下整改办法:
        我司会加强坐席话务话术监管，由专职员工对呼出内容进行审核，增加质检录音
        检查强度，对违规行为重点教育，做到意识教育与行为监督双向管控，杜绝同类
        现象的出现。如再次出现此类事件，我司愿意承担一切责任和处罚。同时我司
        郑重承诺：我司遵守国家相关法律法规，合法合规经营，绝不存在任何违法违
        规的经营，特别是电信诈骗等违法违规行为，否则由此产生的一切后果全部由
        我司承担，与贵方无关。
      </p>
      <p style="margin: 30px 0 10px 0">
        如我司有再次违规的情况，我司接受如下违规处理办法:
      </p>
      <p>1、关停账号；</p>
      <p>2、承担罚款；</p>
      <p style="margin-bottom: 30px">3、终止合作，严重将追究法律责任。</p>
    </div>
    <template #footer>
      <div style="width: 100%; text-align: center">
        <el-button @click="handleClose"
          >（{{ initData.time }}） 我已知晓，立刻缴纳罚款</el-button
        >
      </div>
    </template>
  </el-dialog>
</template>

<script setup lang="ts">
import { reactive } from "vue";

const initData = reactive({
  visible: false,
  time: 60,
  timer: null,
});

const handleClose = () => {
  clearInterval(initData.timer);
  initData.visible = false;
};

const initTime = () => {
  initData.timer = setInterval(() => {
    if (initData.time === 0) {
      handleClose();
    } else {
      initData.time--;
    }
  }, 1000);
};

const toggle = (value) => {
  initData.visible = value;
};

initTime();

defineExpose({
  toggle,
});
</script>

<style scoped lang="scss"></style>
