<template>
  <use-drawer
    ref="useDrawerRef"
    @confirm="confirm"
    @cancel="cancel"
    class="drawer-box"
    title="违规整改"
    :isShowCancelBtn="false"
    :isShowConfirmBtn="false"
    size="700"
  >
    <el-descriptions title="基本情况" border column="2">
      <el-descriptions-item
        v-for="item in topInfo"
        :key="item.prop"
        :label="item.label"
        >{{ item.prop }}</el-descriptions-item
      >
    </el-descriptions>
    <h4 style="margin: 20px 0">整改步骤</h4>
    <div class="tip-container">
      <div class="tip-text" :style="{ color: tip === '1' && '#0056ff' }">
        <div>1</div>
        <div>上传整改资料</div>
      </div>
      <div class="tip-text" :style="{ color: tip === '2' && '#0056ff' }">
        <div>2</div>
        <div>缴纳罚款</div>
      </div>
    </div>
    <div class="tip-container-content">
      <div v-if="tip === '1' && !submitted">
        <div class="tip-container-content-title">业务合规承诺书</div>
        <div style="font-size: 14px; margin: 14px 0">
          下载业务合规承诺书，打印法人签字加盖公司公章，提交扫描件或图片，支持png、jpg、pdf文件格式，大小小于5M
        </div>
        <el-link style="font-size: 14px" type="primary" :underline="false"
          >点击下载</el-link
        >
        <div style="margin: 14px 0">
          <CustomUpload
            @emitUpload="offlinePaymentUploadImg"
            :defaultFileList="initData.defaultFileList"
            listType="picture-card"
            accept=".png,.jpg,.pdf"
            limit="10"
            maxVolume="5"
          ></CustomUpload>
        </div>
        <div>
          <el-button type="primary" @click="submit">提交材料</el-button>
        </div>
      </div>
      <div v-if="tip === '1' && submitted" class="tip-container-content-review">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-certification-waiting"></use>
        </svg>
        <h4>资料已提交，正在审核中</h4>
        <div>审核通过，缴纳罚款后，即可复开</div>
        <el-button @click="toggle(false)">关闭</el-button>
      </div>
      <div v-if="tip === '2' && submitted" class="tip-container-content-review">
        <h4>缴纳金额</h4>
        <h2>￥1000.00</h2>
        <el-button type="primary" @click="toggle(false)">立刻缴纳</el-button>
        <div style="font-size: 14px; color: #333333; margin-top: 30px">
          注：该公司因涉嫌诈骗违规，缴纳罚款后即可复开继续使用。
        </div>
      </div>
    </div>
  </use-drawer>
</template>

<script setup lang="ts">
import UseDrawer from "@components/common/use-drawer.vue";
import { ref, shallowReactive } from "vue";
import { topInfo } from "@assets/typeStatus";
import CustomUpload from "@components/common/custom-upload.vue";
import { ElMessage } from "element-plus";
const initData = shallowReactive({
  defaultFileList: [],
  fileIds: [],
});
const useDrawerRef = ref();
const tip = ref("1"); // 要根据接口判断是否已提交材料 1 未提交 2 已提交
const submitted = ref(false); // 要根据接口判断是否已提交材料
const confirm = () => {};
const cancel = () => {
  initData.defaultFileList = [];
  initData.fileIds = [];
  tip.value = "1";

  // submitted.value = false  是否要根据接口判断是否已提交材料
};
const offlinePaymentUploadImg = (value) => {
  initData.fileIds = value;
};
const submit = () => {
  if (initData.fileIds && initData.fileIds.length === 0) {
    return ElMessage.warning("请上传承诺书");
  }
  submitted.value = true;
};
const toggle = (value) => {
  useDrawerRef.value.toggle(value);
};

defineExpose({
  toggle,
});
</script>

<style scoped lang="scss">
.tip-container {
  background: #f5f7fb;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 8px 0;
  .tip-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.tip-container-content {
  background: #f5f7fb;
  padding: 20px;
  margin-top: 16px;

  &-title:before {
    content: "*";
    display: inline-block;
    color: red;
    margin-right: 4px;
  }

  .tip-container-content-review {
    height: 360px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    svg {
      width: 68px;
      height: 68px;
    }
  }
}
</style>
