<template>
  <el-drawer
    :size="props.size"
    :title="props.title"
    class="custom-drawer"
    :model-value="visible"
    @closed="handleClosed"
  >
    <div class="body-container">
      <slot></slot>
    </div>
    <template #footer>
      <el-button v-if="isShowCancelBtn" @click="handleClose"
        >{{ props.cancelText }}
      </el-button>
      <el-button v-if="isShowConfirmBtn" type="primary" @click="confirm"
        >{{ props.confirmText }}
      </el-button>
    </template>
  </el-drawer>
</template>

<script lang="ts" setup>
import { ref } from "vue";

const emits = defineEmits<{
  (e: "confirm", value?: Record<string, any>): void;
  (e: "cancel", value?: Record<string, any>): void;
}>();
const props = withDefaults(
  defineProps<{
    title: string;
    size: string;
    confirmText: string;
    cancelText: string;
    isShowCancelBtn: boolean;
    isShowConfirmBtn: boolean;
  }>(),
  {
    title: "查看",
    size: "600",
    confirmText: "确认",
    cancelText: "关闭",
    isShowCancelBtn: true,
    isShowConfirmBtn: true,
  }
);
const visible = ref(false);
const handleClose = () => {
  emits("cancel");
  visible.value = false;
};
const confirm = () => {
  emits("confirm");
};
const handleClosed = () => {
  visible.value = false;
  emits("cancel");
};

const toggle = (value: boolean) => {
  visible.value = value;
};

defineExpose({
  toggle,
});
</script>

<style scoped lang="scss"></style>
