<template>
  <div>
    <div class="table-container">
      <!--   列表   -->
      <el-table :data="pageData.list" height="calc(100vh - 268px)">
        <el-table-column
          prop="time"
          label="日期"
          align="center"
        ></el-table-column>
        <el-table-column prop="seat" label="坐席" align="center">
        </el-table-column>
        <el-table-column
          prop="phone"
          label="主叫号码"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="callTime"
          label="通话开始时间"
          align="center"
        ></el-table-column>
        <el-table-column prop="soundRecording" label="录音" align="center">
          <template #default="{ row }">
            <el-link type="primary" :underline="false" @click="down(row)"
              >下载</el-link
            >
            <el-link
              type="primary"
              :underline="false"
              @click="play(row)"
              style="margin-left: 5px"
              >播放</el-link
            >
          </template>
        </el-table-column>
        <el-table-column
          prop="reason"
          label="触发原因"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="result"
          label="触发结果"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="status"
          label="状态"
          align="center"
        ></el-table-column>
        <el-table-column
          label="操作"
          align="center"
          fixed="right"
          width="150"
          class-name="operation-group"
        >
          <template #default="{ row }">
            <el-link
              type="primary"
              :underline="false"
              @click="immediateRectification(row)"
              >立刻整改</el-link
            >
          </template>
        </el-table-column>
        <template #empty>
          <TableEmpty empty-text="暂无数据"></TableEmpty>
        </template>
      </el-table>
      <!--   分页   -->
      <PagePagination
        :pageTotal="pageData.pageTotal"
        :pageSize="pageData.pageSize"
        :pageNumber="pageData.pageNumber"
        @emitPageSizeChange="pageSizeChange"
        @emitPageNumberChange="pageNumberChange"
      >
      </PagePagination>
    </div>
    <!--生效中-立即整改  -->
    <immediate-rectification-drawer ref="drawerRef" />
    <!--审核中-立即整改  -->
    <!--已整改-立即整改  -->

    <!-- 合规业务知晓书 -->
    <know-the-book-drawer ref="bookDrawerRef" />
  </div>
</template>

<script lang="ts" setup>
import { ref, shallowReactive } from "vue";
import { usePage } from "jz-tool-lib";
import { useVueRouter } from "@assets/useHook";
import { speechLibraryListApi } from "@api/business-management-api";
import { LocalStorage } from "jz-tool-lib";
import ImmediateRectificationDrawer from "@views/business-management/osprey-cloud-call/call-quality-testing/components/immediate-rectification-drawer.vue";
import KnowTheBookDrawer from "@views/business-management/osprey-cloud-call/call-quality-testing/components/know-the-book-drawer.vue";

const { routeQuery } = useVueRouter();
const { bd_userInfo: userInfo, bd_virCompanyId: virCompanyId } =
  LocalStorage.batchGetItem([
    "bd_userInfo",
    "bd_virCompanyId",
    "bd_authorization",
  ]);

const drawerRef = ref();
const bookDrawerRef = ref();
const paramsData = shallowReactive({
  title: "",
  status: "",
  companyName: "",
  companyId: virCompanyId,
  companyCode: routeQuery.value.companyCode ?? "",
});
// 获取话术库列表
const { getList, pageNumberChange, pageSizeChange, pageData } = usePage({
  listApi: speechLibraryListApi,
  props: {
    pageNumber: "pageNo",
    pageSize: "pageSize",
  },
  paramsData,
});

const down = (row) => {};
const play = (row) => {};
const immediateRectification = (row = {}) => {
  drawerRef.value.toggle(true, { ...row });
};
</script>
<style lang="scss" scoped></style>
